@charset "utf-8";

*{margin: 0px; padding: 0px;}
ul,li,ol{list-style: none;}
body{
  font-family: "맑은 고딕",Arial,sans-serif;
  font-size: 12px;
  color: #333;
}
a{text-decoration: none; color: #333;}

header{
  width: 100%;
  height: 156px;
  box-shadow: 0px 0px 3px #333;
}
.header_inner{
  width: 1050px;
  height: 100%;
  margin: 0px auto;
  position: relative;
}
.inner_top >.logo_wrap{
  position: absolute;
  top: 40%;
  transform: translateY(-50%);
  padding: 0px;
  width: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
}
.inner_top>.logo_wrap>li>h1{
  position: relative;
  padding-right: 5px;
  padding-top: 10px;
}
.inner_top >.logo_wrap >li{
  font-size: 18px;
  margin-left: 16px;
  color: #ccc;
}
.inner_top >.logo_wrap >li:nth-child(2){
  color: #5f0080;
  font-weight: bold;
}
.login_link{
  font-weight: bold;
  text-align: right;
  margin: 0px auto;
  width: 1050px;
  height: 40px;
  font-size: 12px;
  color: #333;
}
.login_link :any-link{
  margin: 0px 10px;
  text-decoration: none;
  font-size: 12px;
  line-height: 33px;
  color: #333;
}
.login_link span{
  color: #5f0080;
}
.search{
  position: absolute;
  left: 31%;
  top: 45%;
  transform: translateY(-50%);
  z-index: 10;
}
#search{
  width: 400px;
  height: 48px;
  font-size: 14px;
  padding: 15px;
  box-sizing: border-box;
  border-radius: 5px;
  border:1.5px solid #5f0080;
}
.search label {
  position: absolute;
  right: 0px;
}
.search label img{
  width: 50px;
}
.icon_wrap{
  position: absolute;
  right: 0px;
  top: 40%;
  transform: translateY(-50%);
  display: flex;
  justify-content: right;
  align-items: center;
}
.icon_wrap li{
  padding-left: 20px;
}
.inner_menu{
  width: 100%;
  position: absolute;
  bottom: 5px;
}
.inner_menu ul{
  display: flex;
  justify-content: center;
}
.inner_menu ul li{
  position: relative;
  right: 466px;
  font-size: 16px;
  font-weight: bold;
}
.inner_bottom{
  width: 100%;
  position: absolute;
  bottom: 5px;
}
.inner_bottom ul{
  display: flex;
  justify-content: center;
}
.inner_bottom ul li{
  padding: 0px 45px;
  font-size: 16px;
  font-weight: bold;
}


.banner{
  width: 100%;
  height: 140px;
  padding: 40px 0px;
}
.banner img{
  width: 100%;
  height: 100%;
}
main, .product, .notfound{
  /* 컨텐츠 가로기준 1050 */
  width: 1050px;
  padding: 30px 0px;
  /* 가운데 정렬 */
  margin: auto; 
}
.main_slide{
  width: 100%;
  height: 370px;
}
.main_slide img{
  width: 100%;
  height: 100%;
}



.notfound{
  width: 100%;
  height: 700px;
}
.notfound p{
  font-size: 25px;
  text-align: center;
  font-weight: bolder;
}
.notfound p span{
  padding-top: 30px;
  display: block;
  font-size: 16px;
  color: #ccc;
  font-weight: 100;
}
.notfound p input{
  width: 150px;
  height: 44px;
  background-color: #5f0080;
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
  border: none;
}
footer{
  width: 100%;
  height: 580px;
  padding-top: 30px;
  border-top: 1px solid #ccc;
}
.f_inner{
  width: 1050px;
  margin: 0px auto;
  height: 470px;
  position: relative;
}
.address_wrap{
  width: 100%;
  height: 110px;
  background-color: rgb(236, 236, 236);
  text-align: center;
  position: relative;
}
.address_wrap address{
  font-style: normal;
  text-transform: uppercase;
  font-size: 7px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.txt_box1{
  position: absolute;
  top: 0px;
  left: 0px;
}
.txt_box1 h2{
  font-size: 20px;
}
.txt_box1 div{
  font-size: 28px;
  font-weight: bold;
}
.txt_box1 div span{
  font-size: 15px;
  padding-left: 10px;
}
.txt_box2{
  font-weight: bold;
  width: 456px;
  position: absolute;
  top: 0px;
  right: 0px;
}
.txt_box2 ul{
  padding: 0px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  padding-bottom: 30px;
}
.txt_box2 div{
  color: rgb(138, 138, 138);
  line-height: 20px;
}
.color{
  color: #5f0080;
}