@charset "utf-8";

ol, ul, li{
  list-style: none;
}
h3{
  text-align: center;
  font-size: 26px;
  color: #333;
}
/* product 서식 */
.product h2{
  text-align: center;
  font-size: 28px;
  padding-bottom: 10px;
}
.product ul{
  padding-top: 5px;
}
/* product list 서식 */
.product_list{
  width: 1050px;
  height: 434px;
  display: flex;
  justify-content:space-between;
  margin:0px auto;
  padding: 0px;
}
.product_list div{
  position: absolute;
  right: 10px;
  bottom: 130px;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  background-color: #fff;
  border-radius:50%;
  font-size: 15px;
  color: #f00;
  cursor: pointer;
}
.product_list > li p{
  position: relative;
  font-size: 16px;
  top: 10px;
}
.product_list > li .f_name{
  color: #333;
  font-family: "Noto Sans", "malgun gothic", AppleGothic, dotum, sans-serif;
  font-weight: 400;
}
.product_list > li > span{
  display: inline-block;
  position: relative;
  font-size: 16px;
  padding-right: 15px;
  padding-bottom: 15px;
  top: 20px;
}
.product_list > li .sale{
  color: rgb(250, 98, 47);
}
.product_list > li .price{
  color: #f00;
  font-weight: bolder;
}
.product_list > li .b_price{
  color: rgb(124, 124, 124);
  text-decoration: line-through;
}
.product_list > li{
  position: relative;
  color: #333;
  width: 249px;
  height: 434px;
  margin: 0px auto;
  overflow: hidden;
}
.product_list > li img{
  width: 249px;
  transition: all 0.2s linear;
}
.product_list > li img:hover{
  transform: scale(1.05);
  height: auto;
  cursor: pointer;
}
